import React, { useState } from "react";
import '../../settingsDiscount/settingsDiscount'
import settingLogo from '../../img/setting.svg'
import addPictures from '../../img/add-pictyres.svg'
import plusAdd from '../../img/plusAdd.svg'
import AddPromo from "../addPromo/addPromo";


function DiscountAdd(props) {
    const [counter, setCounter] = useState(true);
    const [name, setName] = useState('');
    const [image, setImage] = useState(null);
    const [description, setDescription] = useState('');
    const [link, setLink] = useState('');
    const [barcode, setBarcode] = useState('');
    const [promocodeDescription, setPromocodeDescription] = useState('')
    const [promocode, setPromocode] = useState('');
    const [date, setDate] = useState('');
    const [fullTerms, setFullTerms] = useState('');

    function handleClick() {
        setCounter(!counter)
    }

console.log(image)

    function handleSubmit(e) {
        e.preventDefault();
        props.handleAddDiscount(name, image, description, link, barcode,fullTerms, props.categoryID, promocodeDescription, promocode, date);
    }

    function handleName(e) {
        setName(e.target.value)
    }

    function handleImage(e) {

        setImage(e.target.files[0])
    }

    function handleDescription(e) {

        setDescription(e.target.value);
    }

    function handlePromocodeDescription(e) {
        setPromocodeDescription(e.target.value)
    }

    function handlePromocode(e) {
        setPromocode(e.target.value);
    }

    function handleData(e) {
        setDate(e.target.value);
    }

    function handleLink(e) {
        setLink(e.target.value);
    }

    function handleBarcode(e) {
        setBarcode(e.target.files[0]);

    }
    
    function handleFullTerms(e){
        setFullTerms(e.target.value)
    }


    return (
        <div className="settings">
            <form className="setting" onSubmit={handleSubmit}>
                <div className="setting__logo">
                    <img src={settingLogo} />
                    <p className="setting__text"></p>
                </div>
                <input
                    className="setting__name-discount"
                    type="text"
                    placeholder=" "
                    onChange={handleName}
                />
                <div className="input__file">
                    <input type="file"
                        onChange={handleImage}
                        className="input__file-add"
                        placeholder=" "
                        name="add-file"
                        multiple
                        accept="image/png, image/jpeg"
                        id="add-file" />
                    <img className="input-file-img" src={addPictures} />
                    <label for="add-file" className="input__file-label"> </label>
                </div>
                <textarea
                    className="input__text"
                    id="add-text"
                    name="add-text"
                    placeholder="  ..."
                    onChange={handleDescription}></textarea>
                <p className="input__add-text" for='add-text'> </p>

                {
                    counter ? <></> :
                        <AddPromo
                            handlePromocodeDescription={handlePromocodeDescription}
                            handlePromocode={handlePromocode}
                            handleData={handleData}

                        />

                }
                {
                    counter ?
                        <button className="input__button-promocode" for='add-text' onClick={handleClick} > </button>
                        : <></>
                }

                <input
                    type="text"
                    className="input__add-link"
                    placeholder=" "
                    onChange={handleLink}
                />
                <div className="input__barcode">
                    <input
                        type="file"
                        className="input__barcode-add"
                        placeholder=" "
                        name="add-barcode"
                        multiple
                        accept="image/png, image/jpeg"
                        id="add-barcode"
                        onChange={handleBarcode}
                    />
                    <img className="input-barcode-img" src={plusAdd} />
                    <label for="add-barcode" className="input__file-barcode"> </label>
                </div>
                <div className="full-terms-add">
                    <label className="full-terms-add-title input__add-text">  </label>
                    <textarea
                        type="text"
                        className="full-terms-add-input input__text input__text_description-promocode"
                        placeholder="  "
                        onChange={handleFullTerms}
                    ></textarea>
                </div>
                <div className="setting__buttons setting__buttons_add-discount">
                    <button className="setting__button" ></button>
                </div>
            </form>
        </div>
    );
}

export default DiscountAdd;